@import "~fira/fira.css";
.app {
    margin: 25pt 0;
}

.separator {
    height: 100pt;
}

.card {
    border: 2px solid rgba(0, 0, 0, 0.2);
}

.card-header,
.card-footer {
    background-color: rgba(12, 0, 78, 0.05);
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Fira Sans', sans-serif;
}

.loading-container {
    margin: 170px 0;
}

.loading {
    display: flex;
    font-size: 5em;
    font-weight: 650;
    font-family: 'Fira Sans', sans-serif;
    color: rgba(0, 0, 0, 0.404);
    align-content: center;
    justify-content: center;
}

.error-container {
    margin: 30px 0;
}